.btn {
  width: 252px;
  height: 60px;
  background-color: $color-main;
  box-shadow: 0 0 35px 0 rgba(2, 3, 3, 0.05);
  border-radius: 4px;
  line-height: 40px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.16em;
  color: #fff;
  border: none;
  cursor: pointer;
  transition: background-image 0.4s,
    box-shadow 0.4s,
    color 0.4s,
    border-color 0.4s;

  &:active,
  &:focus {
    outline: none;
  }

  &:hover {
    background-image: linear-gradient(to right, $color-main, $color-blue-1);
    box-shadow: 1px 3px 10px $color-main;
    color: #fff;
    border-color: transparent;
  }

  &--call {
    width: 153px;
    height: 46px;
    background-color: transparent;
    border: 3px solid $color-main;
    font-size: 12px;
    color: $color-main;
  }

  &--more {
    width: 130px;
    height: 40px;
    font-size: 12px;
    letter-spacing: 0.12em;
  }

  &--submit {
    width: 100%;
    height: 70px;
  }
}
