/* bringin */
@font-face {
  font-family: "Bringin";
  font-style: normal;
  font-weight: 400;
  src: local("Open Sans ExtraBold"),
    local("Bringin"),
    url("../fonts/bringin.woff2") format("woff2"),
    url("../fonts/bringin.woff") format("woff");
}
